<template lang="html">
	<!--错误记录-->
  <div class="">
    <div class="headers-wrapper" style="border-bottom: 1px solid #e6e9f2;">
      <headers></headers>
    </div>
    <div id="b-body">
    	<div class="one-title">
    		<div>
    			<h2>
	    			错误记录
	    		</h2>
	    		<span>
	    			/
	    		</span>
	    		<p>
	    			说明说明说明说明说明说明说明说明
	    		</p>
    		</div>
    	</div>
    	<div class="s-body">
    		<el-form :inline="true" :model="formInline" class="demo-form-inline b-el-form">
    			
	    		<el-form :inline="true" :model="formInline" class="demo-form-inline">
					  <el-form-item label="支付单号">
					    <el-input v-model="formInline.user" placeholder="请输入订单号"></el-input>
					  </el-form-item>
					</el-form>
					<el-form :inline="true" :model="formInline" class="demo-form-inline">
					  <el-form-item label="运单号">
						   <el-input v-model="formInline.user" placeholder="选择下单区间" class='long-inner'></el-input>
						   <br />
					  	<el-button type="primary" class='ele-btn'>查询</el-button>
						</el-form-item>
					</el-form>	
					<el-form :inline="true" :model="formInline" class="demo-form-inline">
					  <el-form-item label="错误时间">
					    <el-input v-model="formInline.user" placeholder="选择"></el-input>
					  </el-form-item>
					</el-form>  
					 <el-form :inline="true" :model="formInline" class="demo-form-inline">
					  <el-form-item label="处理状态">
					    <el-input v-model="formInline.user" placeholder="选择"></el-input>
					  </el-form-item>
					</el-form>  
				
				</el-form>
				<hr />
				<el-table
			    :data="tableData2"
			    style="width: 100%"
			    border
			    :row-class-name="tableRowClassName">
			    <el-table-column
			      prop="date"
			      label="支付单号"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="运单号"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="司机姓名"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="司机手机号"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="支付金额"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="申请时间"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="付款时间"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="失败原因"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="失败时间"
			      width="">
			    </el-table-column>
			    <el-table-column
			      prop="name"
			      label="操作"
			      width="">
			    </el-table-column>
			  </el-table>
				
				
    	</div>
    </div>
  </div>
</template>

<script>
import headers from "@/components/header"
export default {
  components:{
    headers
  },
  //表格
  methods: {
      tableRowClassName(row, index) {
        if (index === 1) {
          return 'info-row';
        } else if (index === 3) {
          return 'positive-row';
        }
        return '';
      }
    },
  data() {
      return {
        formInline: {
          user: '',
          region: ''
        },
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        //表格
        tableData2: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }]
      };
    }
}
</script>

<style lang="less" scopt>
	body{
		font-size: 10px;
	}
	#b-body{
		width: 1200px;
		margin: 0 auto;
	}
	.one-title{
		padding: 28px 0;
		display: flex;
		justify-content: space-between;
		color: #bfc3d1;
		>div{
			display: flex;
			align-items: center;
		}
		h2{
			color: #0c2482;
		}
		span{
			margin: 0 8px;
		}
	}
	.s-body{
		font-size: 10px;
		border: 1px solid #ccc;
		box-sizing: border-box;
		width: 1200px;
		background: #FFFFFF;
		padding: 0 22px;
		.b-el-form{
			display: flex;
			justify-content: space-between;
			margin-top: 24px;
		}
		.el-form-item__label{
			color: #8B99C0;
		}
		.el-input__inner{
			background: #F5F6FB;
		}
		.el-form-item{
			position: relative;
		}
		.ele-btn{
			width: 192px;
		}
		.long-inner{
			width: 192px;
			margin-bottom: 24px;
		}
		hr{
			margin-bottom: 24px;
		}
		
		.el-table .info-row {
	    background: #c9e5f5;
	  }
	
	  .el-table .positive-row {
	    background: #e2f0e4;
	  }
  	thead{
  		color: #8591A9;
  	}
	}
</style>



